import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  count: 0, // Initial counter value
};

const counterSlice = createSlice({
  name: 'counter',
  initialState,
  reducers: {
    increment: (state) => {
      state.count += 1; // Increment the counter
    },
    decrement: (state) => {
      state.count -= 1; // Decrement the counter
    },
    reset: (state) => {
      state.count = 0; // Reset the counter to 0
    },
    toggleTrueFalseOnLogin: (state, action) => {
      state.value = action.payload; // Toggle value based on login status
    },
  },
});

// Export actions
export const { increment, decrement, reset, toggleTrueFalseOnLogin } = counterSlice.actions;

// Export the reducer
export default counterSlice.reducer;




