// src/features/admin/adminActions.js

import { createAsyncThunk } from '@reduxjs/toolkit';
import axios from 'axios';

// Action to fetch users (GET request)
export const fetchUsers = createAsyncThunk('admin/fetchUsers', async (_, { rejectWithValue }) => {
    try {
        const response = await axios.get('/api/admin/users');
        return response.data;  // Returning the user data
    } catch (error) {
        return rejectWithValue(error.response.data.message);  // Handling error with rejection
    }
});

// Action to delete a user (DELETE request)
export const deleteUser = createAsyncThunk('admin/deleteUser', async (userId, { rejectWithValue }) => {
    try {
        await axios.delete(`/api/admin/users/${userId}`);
        return userId;  // Returning userId to remove it from the local state
    } catch (error) {
        return rejectWithValue(error.response.data.message);  // Handling error with rejection
    }
});

// Action to reset errors in admin state
export const resetError = () => {
    return (dispatch) => {
        dispatch({ type: 'admin/resetError' });
    };
};


